/* basic defaults for theme styling */
body {
  margin: 0;
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

a#main-content {
  /* anchor link to main content has no height */
  height: 0px;
}

body * {
  box-sizing: border-box; /* padding and margin won't effect the width */
}

#container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
}

.pagep {
  /* set padding for elements that set the site width */
  padding: 0 1.568em;
}

@media all and (min-width: 768px) {
  /* set for desktop screens */
  .pagep {
    padding: 0 7.568em;
  }
}
/* main regions */
.page-header,
.page-main,
.page-footer {
  width: 100%;
}

.page-main {
  flex-grow: 1;
}

/********************/
/* site breakpoints */
/********************/
/* handheld */
@media all and (max-width: 767px) {
  html {
    font-size: 2.6vw;
  }
  .sitew {
    width: 100vw;
  }
  /* /handheld */
}
/* desktop - small - 768 - 1024 */
@media all and (max-width: 1023px) and (min-width: 768px) {
  html {
    font-size: 8.8px; /* 40% */
  }
  .sitew {
    width: 768px;
  }
  /* /small */
}
/* desktop - medium - 1024 - 1366 */
@media all and (max-width: 1364px) and (min-width: 1024px) {
  html {
    font-size: 11.7333333333px; /* 53.333333333333336% */
  }
  .sitew {
    width: 1024px;
  }
  /* /medium */
}
/* desktop -large - 1366+ */
@media all and (max-width: 1919px) and (min-width: 1365px) {
  html {
    font-size: 15.640625px; /* 71.09375% */
  }
  .sitew {
    width: 1365px;
  }
  /* /large */
}
/* desktop - max - 1920+ */
@media all and (min-width: 1920px) {
  html {
    font-size: 22px; /* 100% - all media query body font-size math starts here */
  }
  .sitew {
    width: 1920px;
  }
  /* /max */
}
/********************/
/* /site breakpoints */
/********************/
/**
* Responsive tables.
*/
@media all and (max-width: 767px) {
  /* handheld */
  th.priority-low,
  td.priority-low,
  th.priority-medium,
  td.priority-medium {
    display: none;
  }
}
@media all and (max-width: 1023px) and (min-width: 768px) {
  /* small 768px - 1024p */
  th.priority-low,
  td.priority-low {
    display: none;
  }
}
/********************/
/* site layout */
/********************/
.page-header {
  position: sticky; /* Allocates space for the element, but moves it with you when you scroll */
  top: 0; /* specifies the start position for the sticky behavior - 0 is pretty common */
  z-index: 2;
  height: 8em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header button.menu-btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
  color: #ffffff;
  border: 0px solid #ccc;
  width: 2.5em;
  height: 2.5em;
  font-size: 2em;
  padding: 0;
  z-index: 2;
}

.page-header button.menu-btn:hover {
  background-color: rgba(150, 140, 120, 0.75);
}

.page-header button.menu-btn:active {
  background-color: #968c78;
}

.page-header .block.menu--main {
  display: none;
}

.page-content {
  /* all page content starts here */
}

.region.region-content {
  padding-top: 2em;
  padding-bottom: 2em;
}

.utility-belt {
  padding-top: 0.6em;
  padding-bottom: 0.3em;
  margin-right: -1.568em;
  margin-left: -1.568em;
  padding-right: 1.568em;
  padding-left: 1.568em;
  display: none; /* hide on handheld */
}

.mgr-links {
  display: flex;
  flex-direction: column;
}

.mgr-links {
  /* display: none;  hide this on mobile */
}

.region.region-actions {
  padding-top: 0.3em;
  padding-right: 1.568em;
  padding-bottom: 0.3em;
  padding-left: 1.568em;
  margin-right: -1.568em;
  margin-left: -1.568em;
}

.region-highlighted {
  margin-right: -1.568em;
  margin-left: -1.568em;
  padding-top: 0;
  padding-right: 1.568em;
  padding-bottom: 0;
  padding-left: 1.568em;
}

.region-highlighted .messages {
  margin-top: 0.6em;
  margin-bottom: 0.6em;
}

.region.region-actions .block {
  margin-bottom: 0;
}

.page-footer {
  margin: 0 1.568em 0 1.568em;
  margin-top: auto;
  width: 100%;
}

.page-header nav.menu--footer {
  background-color: orange;
  display: flex;
  justify-content: center;
}

a.anchor {
  display: block;
  position: relative;
  top: -8em;
  visibility: hidden;
}

.pushy {
  background-color: #4f4a47;
}

.sidebar {
  font-size: 1.4em;
  /* begin nested sidebar styles */
  /* end nested sidebar styles */
}
.sidebar h4 {
  background-color: #968c78;
  margin-bottom: 0;
  padding: 0 1em;
  line-height: 2em;
  vertical-align: middle;
  color: white;
  font-size: 1em;
}
.sidebar nav ul.menu > li,
.sidebar nav ul.tabs > li,
.sidebar nav.action-links ul > li {
  margin-bottom: 0;
}

/* ***************************************** */
@media all and (min-width: 768px) {
  /* page layout for desktop screens */
  .page-header {
    height: 15em;
    position: relative;
    display: flex; /* on desktop flex-items are in columns */
    flex-direction: column;
    justify-content: space-evenly;
  }
  .page-header button.menu-btn {
    display: none;
  }
  .page-header .block.menu--main {
    display: flex;
    justify-content: center;
  }
  .page-footer .block.menu--footer {
    display: flex;
    justify-content: center;
  }
  .page-header ul {
    margin: 0 auto;
  }
  .utility-belt {
    margin-right: -7.568em;
    margin-left: -7.568em;
    padding-right: 7.568em;
    padding-left: 7.568em;
    display: block; /* show on desktop */
  }
  .mgr-links {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-content: space-between;
    flex-wrap: wrap;
  }
  .mgr-links > .block {
    /* remove margin from blocks in this region */
    margin-bottom: 0;
  }
  /* define child blocks within mgr-links -- assume managemenu and breadcrumb are not present */
  .mgr-links .block.menu--account {
    flex-basis: 100%;
    order: 2;
    display: flex;
    justify-content: flex-end;
  }
  /* breadcrumb is present - managemenu is not */
  .mgr-links.crumbs .block.menu--account {
    flex-basis: 28%;
    order: 2;
  }
  .mgr-links.crumbs .block.block-system-breadcrumb-block {
    flex-basis: 72%;
    order: 1;
  }
  /* managemenu is present */
  .mgr-links.manager nav.menu--manage,
  .mgr-links.manager .block.block-superfishmanage {
    width: auto;
    flex-basis: 72%;
    order: 1;
  }
  .mgr-links.manager nav.menu--account {
    flex-basis: 28%;
    order: 2;
    justify-content: flex-end;
  }
  .mgr-links.manager.crumbs .block.block-system-breadcrumb-block {
    order: 3;
    flex-basis: 100%;
  }
  /* define child blocks within mgr-links -- assume managemenu and breadcrumb are not present */
  .region.region-actions {
    margin-right: -7.568em;
    margin-left: -7.568em;
    padding-right: 7.568em;
    padding-left: 7.568em;
  }
  .region-highlighted {
    margin-right: -7.568em;
    margin-left: -7.568em;
    padding-right: 7.568em;
    padding-left: 7.568em;
  }
  /********************/
  /* /end site layout for desktop screens */
  /********************/
}
