/* layout formatting */
body {
  font-weight: 100;
  background-color: #4f4a47;
  color: #fff5e4;
}

a:link,
a:visited {
  color: #fff5e4;
}

a:hover {
  color: inherit;
}

strong {
  font-weight: 700;
}

pre {
  /* unset normalize to set for geshifilter */
  font-size: unset;
}

/* define table styles for tables that appear in content */
.cke_editable table,
.region-content table,
table.responsive-enabled,
table.views-table {
  width: 100%;
  /* nested */
  /* /end - nested content table styles */
}
.cke_editable table th,
.cke_editable table td,
.region-content table th,
.region-content table td,
table.responsive-enabled th,
table.responsive-enabled td,
table.views-table th,
table.views-table td {
  border: 0.125em solid #968c78;
  padding: 0.5em;
  font-size: 1.36em;
}
.cke_editable table th,
.region-content table th,
table.responsive-enabled th,
table.views-table th {
  background-color: rgba(150, 140, 120, 0.15);
  text-align: left;
  font-weight: normal;
}
.cke_editable table tr:nth-child(even) td,
.region-content table tr:nth-child(even) td,
table.responsive-enabled tr:nth-child(even) td,
table.views-table tr:nth-child(even) td {
  background-color: rgba(149, 149, 149, 0.15);
}

div.geshifilter {
  margin: 0;
  border: none;
  overflow: auto;
  font-size: 1.36em;
  /* custom */
  padding: 1em;
  border-radius: 0.25em;
  -webkit-border-radius: 1em;
  background-color: rgba(79, 74, 71, 0.85);
}

/* style preformatted code blocks with geshi */
div.geshifilter pre {
  overflow: hidden;
}

/* unset table styles within geshifilter */
.geshifilter table th,
.geshifilter table td {
  border: unset;
  padding: unset;
  font-size: unset;
}

#container {
  /* this is the page container */
  box-shadow: 0 0 0.75em rgba(0, 0, 0, 0.25);
  background-color: #635d59;
}

.page-header {
  border-bottom: 0.25em solid #c9a764;
  background-color: #635d59;
}

.utility-belt {
  background-color: rgba(150, 140, 120, 0.05);
  border-bottom: 0.125em solid rgba(150, 140, 120, 0.8);
}

.page-footer {
  border-top: 0.25em solid #c9a764;
  padding-top: 4em;
  padding-bottom: 4em;
  text-align: center;
  background-color: #635d59;
}

/* /layout formatting */
.page-title {
  /* wrap all h1s in page-title class */
  color: #968c78;
  border-bottom: 0.25em solid rgba(150, 140, 120, 0.5);
}

h1 {
  /* font */
  font-weight: normal;
  font-size: 4.675em;
  color: #c9a764;
  line-height: 1.15em;
  text-align: left;
  letter-spacing: -0.03em;
  word-break: break-word;
  padding: 0.5em 0 0.25em 0;
  margin-bottom: 0;
}

h1 a:link,
h1 a:visited {
  color: #968c78;
  text-decoration: none;
}

h1 a:hover {
  text-decoration: underline;
}

h1 .highlight {
  color: #c9a764;
}

h1 .name {
  color: #c9a764;
  display: block;
}

.block.page-title.wsubtext p {
  text-align: center;
  font-style: italic;
  font-size: 1.7em;
  color: #959595;
  line-height: 1em;
  padding-bottom: 1em;
  margin-top: -1em;
  margin-bottom: 0.25em;
}

h2 {
  font-weight: 100;
  font-size: 3.23em;
  color: #7ba1af;
  line-height: 1.15em;
  letter-spacing: 0.005em;
  border-bottom-style: solid;
  border-bottom-width: 0.25rem;
  border-bottom-color: #968c78;
  padding-bottom: 0.25em;
  clear: both;
}

h2:after {
  display: block;
  width: 100%;
  margin-bottom: 0.6em;
}

.block.section-header,
.section-header {
  margin-bottom: 0;
}

.block.section-header h2,
.section-header h2 {
  text-align: center;
  color: #7ba1af;
  letter-spacing: 0.05em;
  font-weight: 300;
  margin: 0;
  padding: 0.5em 0;
  font-size: 2.4em;
  border-bottom: 0.0625em solid #c9a764;
}

.block.section-header h2 a:link,
.block.section-header h2 a:visited,
.section-header h2 a:link,
.section-header h2 a:visited {
  color: #7ba1af;
  text-decoration: none;
  border: 0.0625em solid rgba(0, 0, 0, 0);
}

.block.section-header h2 a:hover,
.section-header h2 a:hover {
