}

h3 {
  font-weight: 700;
  font-size: 2.04em;
  color: #968c78;
  line-height: 1.15em;
  letter-spacing: -0.02em;
  margin-bottom: 0.6em;
}

h4 {
  font-weight: 500;
  font-size: 1.87em;
  color: #968c78;
  line-height: 1.15em;
  margin-bottom: 1.2em;
}

h5 {
  font-weight: 100;
  font-size: 1.785em;
  color: #968c78;
  line-height: 1.15em;
}

/* ***************************************** */
@media all and (min-width: 768px) {
  /* page layout for desktop screens */
  h1 {
    padding: 0.5em 1.25em 0.25em 0;
  }
  h2 {
    font-size: 3.23em;
  }
  h2.section {
